import React from "react";
import "./BookList.css";

const books = [
    {
        id: 1,
        img: "../Images/imageschap1/Pic2.jpg",
        title: "A Sunny Day down by the River",
        author: "James W. Isaacs ",
        numPages: 5,
        link: "../Pages/chap1page1.html",
    },
    {
        id: 2,
        img: "../Images/eagle.jpg",
        title: "A Sunny Day down by the River, work in progress",
        author: "James W. Isaacs",
        numPages: "5",
        link: "../Pages/chap2page1.html",
    },
];

class BookList extends React.Component {
    render() {
        return (
            <section className="booklist">
                {books.map((book) => {
                    return <Book key={book.id} book={book}></Book>;
                })}
            </section>
        );
    }
}

const Book = (props) => {
    const { title, img, author, numPages, link } = props.book;
    return (
        <article className="book">
            <a id="link" href={link}>
                <img id="bookCover" src={img} alt="" />
                <h1>{title}</h1>
                <h2>{author}</h2>
                <h2>{numPages} pages</h2>
            </a>
        </article>
    );
};

export default BookList;
